import R from 'ramda';


const initialState = {
    ids: [],
    search: ''
};

export default (state = initialState,action)=>{
    switch(action.type){
        case 'FETCH_LAPTOP_SUCCESS':
            return(
                R.merge(state,{
                    ids: R.pluck('id',action.payload)
                })
            );
        case 'SEARCH_LAPTOP' :
        {   console.log("text to be searched is " , action.payload);
            return R.merge(state, {
                search: action.payload
            });
        }
        default:
            return state;
    }
};


